import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
declare var $:any;

@Component({
  selector: 'app-htmlcss',
  templateUrl: './htmlcss.component.html',
  styleUrls: ['./htmlcss.component.scss']
})
export class HtmlcssComponent implements OnInit {

  constructor(  private router : Router) { }

  ngOnInit() {
  $(document).ready(function() {      
  		$('#postsCarousel1').carousel( { interval: false } )
		$('#postsCarousel2').carousel( { interval: false } )
		$('#postsCarousel3').carousel( { interval: false } )
		$('#postsCarousel4').carousel( { interval: false } )		
        $('.next1').click(function(){ $('.carousel1').carousel('next');return false; });
    	$('.prev1').click(function(){ $('.carousel1').carousel('prev');return false; });
    	
    	$('.next2').click(function(){ $('.carousel2').carousel('next');return false; });
    	$('.prev2').click(function(){ $('.carousel2').carousel('prev');return false; });
    	
    	$('.next3').click(function(){ $('.carousel3').carousel('next');return false; });
    	$('.prev3').click(function(){ $('.carousel3').carousel('prev');return false; });
    	
    	$('.next4').click(function(){ $('.carousel4').carousel('next');return false; });
    	$('.prev4').click(function(){ $('.carousel4').carousel('prev');return false; });
    	
    	$('.hover-button1').hover( function() { $(".detail-btn1").addClass("top-class1"); });
    	$('.hover-button1').mouseleave( function() {  $(".detail-btn1").removeClass("top-class1"); });
	});   
  }

  onPsdhtmlcss1() { 
    this.router.navigate(['psd-html-css1']); 
  }
}



