import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


import { HomeComponent } from './home/home.component';
import { AboutusComponent } from './aboutus/aboutus.component';
import { ServicesComponent } from './services/services.component';
import { PortfolioComponent } from './portfolio/portfolio.component';
import { TestimonialsComponent } from './testimonials/testimonials.component';
import { ContactComponent } from './contact/contact.component';
import { HtmlcssComponent } from './htmlcss/htmlcss.component';
import { EmailtemplateComponent } from './emailtemplate/emailtemplate.component';
import { PsdToHtmlCss1Component } from './htmlcss/psd-to-html-css-1/psd-to-html-css-1.component';



const appRoute : Routes = [  
	{ path : '',      		       component : HomeComponent           },
	{ path : 'about-us',           component : AboutusComponent        },
	{ path : 'services',           component : ServicesComponent       },
	{ path : 'portfolio',          component : PortfolioComponent      },
	{ path : 'testimonials',       component : TestimonialsComponent   },
	{ path : 'contact',            component : ContactComponent        },
	{ path : 'html-css',           component : HtmlcssComponent        },
	{ path : 'email-template',     component : EmailtemplateComponent  },
	{ path : 'psd-html-css1',   component : PsdToHtmlCss1Component  }
];

@NgModule({
   imports :[
     RouterModule.forRoot( appRoute  , { useHash : false } )
   ],
   exports : [RouterModule]
})
export class AppRoutingModule {
   
}




