import { Component, OnInit } from '@angular/core';
declare var $:any;

@Component({
  selector: 'app-emailtemplate',
  templateUrl: './emailtemplate.component.html',
  styleUrls: ['./emailtemplate.component.scss']
})
export class EmailtemplateComponent implements OnInit {

  constructor() { }

  ngOnInit() {
  	$(document).ready(function() {      
  		$('#postsCarousel1').carousel( { interval: false } )
		  $('#postsCarousel2').carousel( { interval: false } )
		  $('#postsCarousel3').carousel( { interval: false } )
		
		$('.next1').click(function(){ $('.carousel1').carousel('next');return false; });
    	$('.prev1').click(function(){ $('.carousel1').carousel('prev');return false; });
    	
    	$('.next2').click(function(){ $('.carousel2').carousel('next');return false; });
    	$('.prev2').click(function(){ $('.carousel2').carousel('prev');return false; });
    	
    	$('.next3').click(function(){ $('.carousel3').carousel('next');return false; });
    	$('.prev3').click(function(){ $('.carousel3').carousel('prev');return false; });
    	
      $('.hover-button').hover( function() { $(".detail-btn").addClass("top-class"); });
      $('.hover-button').mouseleave( function() {  $(".detail-btn").removeClass("top-class"); });
	  }); 
  }

}



