import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
declare var $:any;

@Component({
  selector: 'app-portfolio',
  templateUrl: './portfolio.component.html',
  styleUrls: ['./portfolio.component.scss']
})
export class PortfolioComponent implements OnInit {

    constructor(  private router : Router) { }

  ngOnInit() {
  	$(document).ready(function() {      
        $('.next').click(function(){ $('.carousel').carousel('next');return false; });
    	$('.prev').click(function(){ $('.carousel').carousel('prev');return false; });
    	$('.hover-button').hover( function() { $(".deatial-btn").addClass("top-class"); });
    	$('.hover-button').mouseleave( function() {  $(".deatial-btn").removeClass("top-class"); });

    	$('.hover-button1').hover( function() { $(".detail-btn1").addClass("top-class1"); });
    	$('.hover-button1').mouseleave( function() {  $(".detail-btn1").removeClass("top-class1"); });
	});   	
  }

}



